import React from 'react';

import './Terms.css';

// import PieChart from '../images/PieChart.png';

const Terms2 = () => (
    <section className="Terms grey_bg">
        <div className="container">
            <h1 className="text-center">Token Sale Terms</h1>

            <h2 className="text-center">Public Token Sale is capped at $33 million USD <br/><br/>Total token supply is approximately 12 billion</h2>
        </div>
    </section>
);

export default Terms2;



